.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Ubuntu", sans-serif;
}

body {
  margin: 0;
  padding: 0;
  background-color: #000;
  /* background-image: url("/background.jpeg"); */
}

.board {
  align-items: row;
  width: 500px;
  height: 500px;
  background-image: linear-gradient(60deg, #7740be, #c56d55);
  border: 1px solid #000;
  display: flex;
  flex-direction: column;
}

.row {
  flex: 33%;
  display: flex;
  flex-direction: row;
}

.square {
  flex: 33%;
  height: 100%;
  border: 1px solid #000;
  cursor: pointer;
  display: grid;
  place-items: center;
  font-size: 50px;
  font-weight: bold;
  color: #630436;
}

.square:active {
  background-image: linear-gradient(235deg, #c56d55, #7740be);
}

.square:hover {
  color: #a1054a;
}
